@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.boutton {
    position: fixed;
    top: 10px; 
    left: 10px; 
    display: flex;
    align-items: center;
    cursor: pointer;
    font-family: "Poppins", Arial, sans-serif;
    color: white;
    font-size: 18px;
    border: none;
    z-index: 200; 
    margin-top: 36px;
}

.barre {
    width: 50px;
    height: 9px;
    background-color: white;
    margin-bottom: 9px;
    border-radius: 5px;
}

.menu-icon {
    width: 25px;
    height: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

ul{
    position: fixed;
    z-index: 1;
    background-color: white;
    border-radius: 10px;
    margin: 0;
    width: 13%;
    list-style-type: none;
    
}

li{
    padding-bottom: 10px;
    padding-top: 10px;
    transition: padding-right 2s ease-in-out;
}

li.hover{
    padding-right: 20px;
    cursor: pointer;
}


@media screen and (max-width: 915px) {
    .boutton {
        top: 10px;
        left: 10px;
        font-size: 13px; 
        position: absolute;
    }

    .barre {
        width: 25px; 
        height: 6px; 
        margin-bottom: 3px; 
        border-radius: 3px; 
    }

    .menu-icon {
        width: 15px; 
        height: 10px; 
    }
}

/* Styles pour les Ã©crans avec une largeur maximale de 280 pixels (Samsung Galaxy Fold) */
@media screen and (max-width: 280px) {
    .boutton {
        top: 5px; 
        left: 5px; 
        font-size: 11px;
        position: absolute; 
    }

    .barre {
        width: 20px; 
        height: 4px; 
        margin-bottom: 2px; 
        border-radius: 2px; 
    }

    .menu-icon {
        width: 10px; 
        height: 7px; 
    }
}

