/* ./css/ProgressBar.css */

.progressContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background-color: black; 
  z-index: 1;
  padding-bottom: 10px;
}

.progress {
  height: 100%;
  width: 0; 
  background-color: white; 
  z-index: 1;
  margin-top: 0;
  padding-bottom: 10px;
}
