@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.conteneur{
    background-color: #a0c8e0;
    padding: 20px; 
    border-radius: 40px;
    margin-top: 10px;
}

.boite{
    border-radius: 20px;
}

body{
    font-family: "Poppins", Arial, sans-serif;
    margin: 0;
    padding: 0;
}

.titre{
    color: white;
    font-size: 50px;
}

.io{
    background-color: #1f3a66;
    margin-top: 20px;
}

.elpepe{
    font-size: 27px;
    color: white;
    position: absolute;
    top: 10px;
    right: 10px;
}

.iconic {
    position: fixed;
    bottom: 10px;
    right: 10px; 
    width: 50px; 
    height: auto; 
    z-index: 200; 
}    

header{
    background-color: white;
    padding-top: 20px;
    border-bottom-left-radius: 70%;
    border-bottom-right-radius: 70%;
    padding-bottom: 70px;
}

.elpipi{
    color: white;
    font-family: "Poppins", Arial, sans-serif;
    text-align: center;
}

footer{
    background-color: black;
    padding-bottom: 30px;
}

.elpopo{
    text-decoration: underline;
}

@media screen and (max-width: 915px) {
    .conteneur {
        margin-top: 10px; 
        padding: 10px; 
        border-radius: 20px; 
    }

    .elpepe {
        font-size: 13px; 
    }

    .iconic {
        position: fixed;
        bottom: 10px; 
        right: 10px;
        width: 40px;
        height: auto;
    }

    .card {
        margin-bottom: 10px; 
    }

    .titre{
        font-size: 30px;
    }

    .card-image img {
        max-width: 100%; 
        height: auto; 
    }
}

@media screen and (min-width: 280px) {
        .conteneur {
            margin-top: 5px;
            padding: 5px; 
            border-radius: 15px; 
        }
    
        .elpepe {
            font-size: 11px; 
        }
    
        .iconic {
            width: 30px;
        }
    
        .card {
            margin-bottom: 5px; 
        }
    
        .titre{
            font-size: 26px; 
        }
    
        .card-image img {
            max-width: 100%;
        }
}

